@import '~node_modules/modern-normalize/modern-normalize.css';

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: $main-font;
  // font-size: 14px;
  // font-weight: 500;
  background: $main-color;
}

// img {
//   display: block;
//   max-width: 100%;
//   height: auto;
// }

.container {
  position: relative;
  width: 400px;
  padding: 30px;
  background: $contacts-color;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 1px solid $main-color;
  gap: 10px;
  padding-bottom: 70px;
  --webkit-box-reflect: below 1px linear-gradient
  (transparent, transparent, #0005);

  @media screen and (min-width: 480px) and (max-width: 767px) {
    max-width: 480px;
  }

  @media screen and (min-width: 768px) and (max-width: 1199px) {
    width: 768px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
